import React from 'react';
import styles from './Header.module.css';

const Header: React.FC = () => {
    return (
        <div className={styles.Header}>
            <div className={styles.nameBox}>
                <div className={`${styles.headerFont} ${styles.headerNameFont}`} >
                    <span></span>
                </div>
                <div className={styles.logo}>
                    <img src="/images/logo.png" style={{ width: '40%', height: 'auto' }} />
                </div>
                <div className={`${styles.headerFont} ${styles.headerNameFont}`} >
                    <span></span>
                </div>
            </div>
            <div className={styles.imgbox}>
                <img src="/images/main.jpg" style={{ width: '100%', height: 'auto' }} />
            </div>
            <div className={`${styles.headerLocationDiv} ${styles.headerFont} ${styles.headerLocationFont}`} >
                <span>2024 10 5   12:30</span>
                <span> 2 </span>
            </div>
        </div>
    );
}

export default Header;
