import React from 'react';
import styles from './Footer.module.css';
import { Button, Divider } from '@mui/material';

const Footer: React.FC = () => {
    const handleCopy = () => {
        navigator.clipboard.writeText(window.location.href); //  
        alert(' .');
    }
    const handleShare = () => {
        Kakao.Share.sendDefault({
            objectType: 'feed',
            content: {
                title: '    ',
                description: '2024 10 5 () 12:30pm\n 2 ',
                imageUrl: `${window.location.origin}/images/main.jpg?t=${new Date().getTime()}`,
                link: {
                    mobileWebUrl: window.location.href,
                    webUrl: window.location.href,
                },
            },
            buttons: [
                {
                    title: '  ',
                    link: {
                        mobileWebUrl: window.location.href,
                        webUrl: window.location.href,
                    },
                }
            ],
        });
    };
    return (
        <div className={styles.Footer}>
            <Button
                variant="contained"
                onClick={handleShare}
                sx={{
                    color: '#ffffff',
                    padding: '2vw 1vw',
                    fontSize: '4vw',
                    fontWeight: 600,
                    backgroundColor: '#666666',
                    width: '50%',
                    textTransform: 'none',
                    boxShadow: 'none',
                    ":active": {
                        backgroundColor: '#666666',
                        borderColor: '#666666',
                        color: '#ffffff'
                    },
                    ":hover": {
                        backgroundColor: '#666666',
                        borderColor: '#666666',
                        color: '#ffffff'

                    }
                }}
            >
                TALK
            </Button>
            <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center', height: '3vw' }}>
                <Divider orientation='vertical' flexItem sx={{ width: '1vw', height: '3vw', color: 'white', marginLeft: 3, marginRight: 3, borderColor: 'white' }} />
            </div>
            <Button
                variant="contained"
                onClick={handleCopy}
                sx={{
                    color: '#ffffff',
                    padding: '2vw 1vw',
                    fontSize: '4vw',
                    fontWeight: 600,
                    backgroundColor: '#666666',
                    width: '50%',
                    textTransform: 'none',
                    boxShadow: 'none',
                    ":active": {
                        backgroundColor: '#666666',
                        borderColor: '#666666',
                        color: '#ffffff'
                    },
                    ":hover": {
                        backgroundColor: '#666666',
                        borderColor: '#666666',
                        color: '#ffffff'
                    }
                }}
            >
                 
            </Button>
        </div>
    );
}

export default Footer;
